 @mixin flex($a, $b, $c, $d){
    display: flex;
    flex-direction: $a; //row
    justify-content: $b; //start
    align-items: $c; //stretch
    flex-wrap: $d; //nowrap
}

@mixin relative {
    position: relative;
}

@mixin absolute($a, $b, $c, $d, $e) {
    position: absolute;
    top: $a; left: $b;
    bottom: $c; right: $d;
    z-index: $e;
}

@mixin fixed($a, $b, $c, $d, $e) {
    position: fixed;
    top: $a; left: $b;
    bottom: $c; right: $d;
    z-index: $e;
}