// reset
@charset "utf-8";

@import '_mixin.scss';

// Noto-Sans, Alrial Black
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;400;500;700;900&display=swap');

*{
    box-sizing: border-box;
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 0;
    line-height: 1.2;
    text-transform: uppercase;
    cursor: none;
}
*::before, *::after{
	border-spacing: 0;
}
html, body, header, main, footer, 
h1, h2, h3, h4, h5, h6, figure, 
div, span, p, a, input, dl, dt, dd{
    margin: 0; padding: 0;
}
img, video, iframe{
    width: 100%; height: 100%;
    margin: 0; padding: 0;
    vertical-align: middle;
    object-fit: cover;
}
a{text-decoration: none; color: inherit;}
ol,ul,li{
    margin: 0; padding: 0;
    list-style-type: none;
}
table {
    border-collapse: collapse;
	border-spacing: 0;
}
textarea, input, button{
    border: none;
    background: none;
    &:focus{
        outline: none;
    }
}
select, select:focus, option{
    border: none;
    background-color: #fff;
}
html::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera*/
}
html{
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
}
// ================================================================================
// ============================= common ===========================================
// ================================================================================

// color
$mc: #0000FF;
$sc: #41FF08;
$mt: #000000;
$st: #ADADAD;
$sst: #E5E5E5;
$w: #ffffff;

// width
.width{
    max-width: 1320px;
    margin: 0 auto;
    padding: 0 20px
}

h1{
    display: inline-block;
    font-family: Arial Black,Arial Bold,Gadget,sans-serif;
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 2px;
    color: $mc;
}
h2{
    display: inline-block;
    font-family: Arial Black,Arial Bold,Gadget,sans-serif;
    font-size: 18px;
    font-weight: 900;
    letter-spacing: 2px;
    color: $mt;
}
h4{
    font-family: Arial Black,Arial Bold,Gadget,sans-serif;
    font-size: 24px;
    font-weight: 800;
    letter-spacing: 0.02em;
    line-height: 1.6;
}
::selection{
    background: $mt;
    color: $w;
}

// ================================================================================
// ============================= header ===========================================
// ================================================================================

header{
    height: 80px;
    overflow: hidden;    
    @include flex(row, space-between, flex-end, wrap);
    .menu{
        width: 50%;
        @include flex(row, space-between, flex-end, nowrap);
        a{
            // height: 80px;
            @include relative;
        }
        a::after{
            content:'';
            width: 120px; height: 12px;
            background-color: $sc;
            display: block;
            @include absolute(140, calc(50% - 60px), -70, 0, -100);
        }
        a:hover::after{
            top: 70; bottom: 0;
        }
        a.active::after{
            content:'';
            width: 120px; height: 12px;
            background-color: $sc;
            display: block;
            @include absolute(70, calc(50% - 60px), 0, 0, -1);
        }
    }
}
header{
    height: 80px; overflow: hidden;
    vertical-align: bottom; 
}
// ================================================================================
// ============================= footer ===========================================
// ================================================================================

footer{
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p{
        color: $st;
    }
    div{
        width: 200px;
        display: flex;
        justify-content: space-between;
        img{
            width: 18px; height: 18px;
        }
    }
}